// Enhanced Dashboard Component with Professional Features
function Dashboard() {
  const { showSuccess, showError, showInfo } = useNotification();
  const [stats, setStats] = useState({
    totalVendors: 0,
    activeVendors: 0,
    pendingVendors: 0,
    totalContracts: 0,
    activeContracts: 0,
    totalSpend: 0
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [recentVendors, setRecentVendors] = useState([]);
  const [refreshing, setRefreshing] = useState(false);
  const [lastUpdated, setLastUpdated] = useState(null);

  const fetchStats = async (isRefresh = false) => {
    try {
      if (isRefresh) {
        setRefreshing(true);
      } else {
        setLoading(true);
      }
      setError(null);

      // Parallel data fetching for vendor management
      const [vendorsSnapshot, contractsSnapshot] = await Promise.all([
        getDocs(collection(db, 'vendors')),
        getDocs(query(collection(db, 'contracts'), orderBy('createdAt', 'desc')))
      ]);

      const vendorsData = [];
      let activeVendorCount = 0;
      let pendingVendorCount = 0;
      let totalSpend = 0;
      let activeContractCount = 0;
      const vendorDistribution = {};
      const monthlySpend = {};

      vendorsSnapshot.forEach((doc) => {
        const vendor = { id: doc.id, ...doc.data() };
        vendorsData.push(vendor);

        if (vendor.status === 'active') activeVendorCount++;
        if (vendor.status === 'pending') pendingVendorCount++;

        // Vendor category distribution
        const category = vendor.category || 'Unknown';
        vendorDistribution[category] = (vendorDistribution[category] || 0) + 1;
      });

      contractsSnapshot.forEach((doc) => {
        const contract = { id: doc.id, ...doc.data() };

        if (contract.status === 'active') activeContractCount++;

        if (contract.value && !isNaN(parseFloat(contract.value))) {
          totalSpend += parseFloat(contract.value);
        }

        // Monthly spend data
        if (contract.createdAt) {
          const date = contract.createdAt.toDate ? contract.createdAt.toDate() : new Date(contract.createdAt);
          const monthKey = date.toLocaleDateString('en-US', { year: 'numeric', month: 'short' });
          const value = parseFloat(contract.value) || 0;
          monthlySpend[monthKey] = (monthlySpend[monthKey] || 0) + value;
        }
      });

      // Get recent vendors (last 5) with proper date handling
      const sortedVendors = vendorsData
        .filter(vendor => vendor.createdAt)
        .sort((a, b) => {
          const dateA = a.createdAt?.toDate ? a.createdAt.toDate() : new Date(a.createdAt);
          const dateB = b.createdAt?.toDate ? b.createdAt.toDate() : new Date(b.createdAt);
          return dateB - dateA;
        })
        .slice(0, 5);

      setRecentVendors(sortedVendors);
      setStats({
        totalVendors: vendorsSnapshot.size,
        activeVendors: activeVendorCount,
        pendingVendors: pendingVendorCount,
        totalContracts: contractsSnapshot.size,
        activeContracts: activeContractCount,
        totalSpend: totalSpend,
        vendorDistribution,
        monthlySpend
      });
      setLastUpdated(new Date());
      if (isRefresh) {
        showSuccess('Dashboard data refreshed successfully');
      }
    } catch (error) {
      console.error("Error fetching dashboard stats:", error);
      const errorMessage = error.message || 'Failed to load dashboard data';
      setError(errorMessage);
      showError(`Dashboard Error: ${errorMessage}`);
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  useEffect(() => {
    fetchStats();

    // Auto-refresh every 5 minutes
    const interval = setInterval(() => fetchStats(true), 5 * 60 * 1000);
    return () => clearInterval(interval);
  }, []);

  const handleRefresh = () => {
    fetchStats(true);
  };

  return (
    <AdminLayout title="Dashboard" activeNav="dashboard">
      <div className="p-6">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-bold text-gray-800">Dashboard Overview</h2>
          <div className="flex items-center space-x-4">
            {lastUpdated && (
              <span className="text-sm text-gray-500">
                Last updated: {lastUpdated.toLocaleTimeString()}
              </span>
            )}
            <button
              onClick={handleRefresh}
              disabled={refreshing}
              className="flex items-center px-3 py-2 bg-indigo-100 text-indigo-700 rounded-lg hover:bg-indigo-200 disabled:opacity-50 transition-colors"
            >
              <FiRefreshCw className={`mr-2 ${refreshing ? 'animate-spin' : ''}`} />
              {refreshing ? 'Refreshing...' : 'Refresh'}
            </button>
          </div>
        </div>

        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <div className="flex items-center">
              <FiAlertCircle className="text-red-600 mr-3" />
              <div>
                <h3 className="text-sm font-medium text-red-800">Error loading dashboard data</h3>
                <p className="text-sm text-red-700 mt-1">{error}</p>
              </div>
              <button
                onClick={handleRefresh}
                className="ml-auto text-red-600 hover:text-red-800"
              >
                <FiRefreshCw className="h-5 w-5" />
              </button>
            </div>
          </div>
        )}

        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
          </div>
        ) : (
          <>
            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-6 gap-6 mb-8">
              <StatCard
                title="Total Vendors"
                value={stats.totalVendors}
                icon={<FiUsers className="text-indigo-600" />}
                color="indigo"
                change={"+3 this week"}
              />
              <StatCard
                title="Active Vendors"
                value={stats.activeVendors}
                icon={<FiCheckCircle className="text-green-600" />}
                color="green"
                change={"+12% from last month"}
              />
              <StatCard
                title="Pending Vendors"
                value={stats.pendingVendors}
                icon={<FiClock className="text-yellow-600" />}
                color="yellow"
                change={"Need review"}
              />
              <StatCard
                title="Total Contracts"
                value={stats.totalContracts}
                icon={<FiFileText className="text-blue-600" />}
                color="blue"
                change={"+5 this month"}
              />
              <StatCard
                title="Active Contracts"
                value={stats.activeContracts}
                icon={<FiFile className="text-purple-600" />}
                color="purple"
                change={"Currently running"}
              />
              <StatCard
                title="Total Spend"
                value={`${stats.totalSpend.toLocaleString()}`}
                icon={<FiCreditCard className="text-red-600" />}
                color="red"
                change={"+8% from last month"}
              />
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
              {/* Spend Chart */}
              <div className="lg:col-span-2 bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                <div className="flex justify-between items-center mb-6">
                  <h3 className="text-lg font-semibold text-gray-800">Monthly Spend Overview</h3>
                  <select className="text-sm border border-gray-300 rounded-md px-3 py-1">
                    <option>Last 6 months</option>
                    <option>Last 12 months</option>
                    <option>This year</option>
                  </select>
                </div>
                <div className="h-64">
                  <SpendChart data={stats.monthlySpend} />
                </div>
              </div>

              {/* Vendor Distribution */}
              <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                <h3 className="text-lg font-semibold text-gray-800 mb-6">Vendor Categories</h3>
                <div className="h-64">
                  <VendorDistributionChart data={stats.vendorDistribution} />
                </div>
              </div>
            </div>

            {/* Recent Activity */}
            <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg font-semibold text-gray-800">Recent Vendors</h3>
                <Link to="/admin/vendors" className="text-sm text-indigo-600 hover:text-indigo-800">
                  View all
                </Link>
              </div>

              {recentVendors.length > 0 ? (
                <div className="overflow-hidden">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                      <tr>
                        <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Vendor Name</th>
                        <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Category</th>
                        <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                        <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Date Added</th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {recentVendors.map((vendor) => (
                        <tr key={vendor.id}>
                          <td className="px-4 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                            {vendor.name || 'Unknown Vendor'}
                          </td>
                          <td className="px-4 py-4 whitespace-nowrap text-sm text-gray-900">
                            {vendor.category || 'N/A'}
                          </td>
                          <td className="px-4 py-4 whitespace-nowrap">
                            <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full 
                              ${vendor.status === 'active' ? 'bg-green-100 text-green-800' :
                                vendor.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                                  'bg-gray-100 text-gray-800'}`}>
                              {vendor.status}
                            </span>
                          </td>
                          <td className="px-4 py-4 whitespace-nowrap text-sm text-gray-900">
                            {vendor.createdAt?.toDate().toLocaleDateString()}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              ) : (
                <div className="text-center py-8 text-gray-500">
                  <FiInfo className="mx-auto h-12 w-12 mb-4" />
                  <p>No recent vendors found</p>
                </div>
              )}
            </div>
          </>
        )}
      </div>
    </AdminLayout>
  );
}




function RevenueChart({ data }) {
  const chartData = Object.entries(data || {})
    .map(([date, revenue]) => ({
      date: new Date(date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }),
      revenue: revenue
    }))
    .slice(-7); // Last 7 days

  if (chartData.length === 0) {
    return (
      <div className="h-full flex items-center justify-center bg-gray-50 rounded-lg">
        <div className="text-center text-gray-500">
          <FiBarChart2 className="mx-auto h-12 w-12 mb-2" />
          <p>No revenue data available</p>
        </div>
      </div>
    );
  }

  return (
    <ResponsiveContainer width="100%" height="100%">
      <AreaChart data={chartData}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="date" />
        <YAxis />
        <Tooltip formatter={(value) => [`${value}`, 'Revenue']} />
        <Area type="monotone" dataKey="revenue" stroke="#4F46E5" fill="#4F46E5" fillOpacity={0.1} />
      </AreaChart>
    </ResponsiveContainer>
  );
}

function ServiceDistributionChart({ data }) {
  const chartData = Object.entries(data || {})
    .map(([service, count]) => ({
      name: service.length > 15 ? service.substring(0, 15) + '...' : service,
      value: count
    }))
    .slice(0, 6); // Top 6 services

  const COLORS = ['#4F46E5', '#06B6D4', '#10B981', '#F59E0B', '#EF4444', '#8B5CF6'];

  if (chartData.length === 0) {
    return (
      <div className="h-full flex items-center justify-center bg-gray-50 rounded-lg">
        <div className="text-center text-gray-500">
          <FiPieChart className="mx-auto h-12 w-12 mb-2" />
          <p>No service data available</p>
        </div>
      </div>
    );
  }

  return (
    <ResponsiveContainer width="100%" height="100%">
      <PieChart>
        <Pie
          data={chartData}
          cx="50%"
          cy="50%"
          labelLine={false}
          label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
          outerRadius={80}
          fill="#8884d8"
          dataKey="value"
        >
          {chartData.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>
        <Tooltip />
      </PieChart>
    </ResponsiveContainer>
  );
}

