// Service Details Component
function ServiceDetails() {
  const { id } = useParams();
  const [service, setService] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchService = async () => {
      try {
        const serviceDoc = await getDoc(doc(db, 'services', id));
        if (serviceDoc.exists()) {
          setService({ id: serviceDoc.id, ...serviceDoc.data() });
        } else {
          setError('Service not found');
        }
      } catch (err) {
        console.error('Error fetching service:', err);
        setError('Failed to load service details');
      } finally {
        setLoading(false);
      }
    };

    fetchService();
  }, [id]);

  if (loading) {
    return (
      <AdminLayout title="Service Details" activeNav="services">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
        </div>
      </AdminLayout>
    );
  }

  if (error) {
    return (
      <AdminLayout title="Service Details" activeNav="services">
        <div className="bg-red-50 border-l-4 border-red-500 p-4 mb-6 rounded">
          <p className="text-red-700">{error}</p>
        </div>
      </AdminLayout>
    );
  }

  return (
    <AdminLayout title="Service Details" activeNav="services">
      <div className="p-6">
        <div className="flex justify-between items-start mb-6">
          <h2 className="text-2xl font-bold text-gray-800">{service.name}</h2>
          <Link
            to={`/admin/edit-service/${id}`}
            className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 flex items-center"
          >
            <FiEdit2 className="mr-2" /> Edit Service
          </Link>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2">
            <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 mb-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">Service Information</h3>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Price</label>
                  <p className="text-gray-900">{service.price}</p>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Processing Time</label>
                  <p className="text-gray-900">{service.processingTime}</p>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Status</label>
                  <span className="px-2 py-1 bg-green-100 text-green-800 text-xs font-medium rounded-full">
                    Active
                  </span>
                </div>
              </div>
            </div>

            <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">Description</h3>
              <p className="text-gray-700">{service.description}</p>
            </div>
          </div>

          <div>
            <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 mb-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">Key Benefits</h3>
              <ul className="list-disc list-inside space-y-2 text-gray-700">
                {service.keyBenefits && service.keyBenefits.map((benefit, index) => (
                  <li key={index}>{benefit}</li>
                ))}
              </ul>
            </div>

            <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">Required Documents</h3>
              <ul className="list-disc list-inside space-y-2 text-gray-700">
                {service.documentsRequired && service.documentsRequired.map((doc, index) => (
                  <li key={index}>{doc}</li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </AdminLayout>
  );
}

// Service List Component (Enhanced)
function ServiceList() {
  const [services, setServices] = useState([]);
  const [filteredServices, setFilteredServices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('name');
  const [deleteLoading, setDeleteLoading] = useState(null);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    fetchServices();
  }, []);

  useEffect(() => {
    filterAndSortServices();
  }, [services, searchTerm, sortBy]);

  const fetchServices = async () => {
    try {
      setLoading(true);
      setError('');
      const q = query(collection(db, 'services'), orderBy('name'));
      const querySnapshot = await getDocs(q);
      const servicesData = [];
      querySnapshot.forEach((doc) => {
        servicesData.push({ id: doc.id, ...doc.data() });
      });
      setServices(servicesData);
    } catch (error) {
      console.error("Error fetching services: ", error);
      setError('Failed to load services. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const filterAndSortServices = () => {
    let result = [...services];

    // Apply search filter
    if (searchTerm) {
      const term = searchTerm.toLowerCase();
      result = result.filter(service =>
        service.name.toLowerCase().includes(term) ||
        service.description.toLowerCase().includes(term)
      );
    }

    // Apply sorting
    result.sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.name.localeCompare(b.name);
        case 'price-low':
          return a.price - b.price;
        case 'price-high':
          return b.price - a.price;
        default:
          return 0;
      }
    });

    setFilteredServices(result);
  };

  const handleDelete = async (id) => {
    if (window.confirm('Are you sure you want to delete this service? This action cannot be undone.')) {
      try {
        setDeleteLoading(id);
        await deleteDoc(doc(db, 'services', id));

        // Also delete associated form if it exists
        try {
          await deleteDoc(doc(db, 'forms', id));
        } catch (err) {
          console.log('No form found for this service');
        }

        setServices(services.filter(service => service.id !== id));
        setSuccess('Service deleted successfully');
        setTimeout(() => setSuccess(''), 3000);
      } catch (error) {
        console.error("Error deleting service: ", error);
        setError('Failed to delete service. Please try again.');
      } finally {
        setDeleteLoading(null);
      }
    }
  };

  const handleEdit = (id) => {
    navigate(`/admin/edit-service/${id}`);
  };

  const handleViewDetails = (id) => {
    navigate(`/admin/service-details/${id}`);
  };

  if (loading && services.length === 0) {
    return (
      <AdminLayout title="Manage Services" activeNav="services">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
        </div>
      </AdminLayout>
    );
  }

  return (
    <AdminLayout title="Manage Services" activeNav="services">
      {error && (
        <div className="bg-red-50 border-l-4 border-red-500 p-4 mb-6 rounded flex justify-between items-center">
          <p className="text-red-700">{error}</p>
          <button onClick={() => setError('')} className="text-red-600">
            <FiXCircle size={20} />
          </button>
        </div>
      )}

      {success && (
        <div className="bg-green-50 border-l-4 border-green-500 p-4 mb-6 rounded flex justify-between items-center">
          <p className="text-green-700">{success}</p>
          <button onClick={() => setSuccess('')} className="text-green-600">
            <FiXCircle size={20} />
          </button>
        </div>
      )}

      {/* Header with Add Button and Filters */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-6">
        <h2 className="text-2xl font-bold text-gray-800 mb-4 sm:mb-0">All Services</h2>
        <Link
          to="/admin/add-service"
          className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 flex items-center"
        >
          <FiPlus className="mr-2" />
          Add New Service
        </Link>
      </div>

      {/* Filters and Search */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-4 mb-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="relative">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <FiSearch className="text-gray-400" />
            </div>
            <input
              type="text"
              placeholder="Search services..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10 pr-4 py-2 w-full border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
            />
          </div>

          <div>
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
            >
              <option value="name">Sort by Name</option>
              <option value="price-low">Price: Low to High</option>
              <option value="price-high">Price: High to Low</option>
            </select>
          </div>
        </div>
      </div>

      {/* Services Count and Refresh Button */}
      <div className="flex justify-between items-center mb-4">
        <p className="text-sm text-gray-600">
          Showing {filteredServices.length} of {services.length} services
        </p>
        <button
          onClick={fetchServices}
          className="flex items-center text-indigo-600 hover:text-indigo-800 text-sm"
        >
          <FiRefreshCw className="mr-1" /> Refresh
        </button>
      </div>

      {/* Services Table */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
        {filteredServices.length > 0 ? (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Service
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Price
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Processing Time
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredServices.map((service) => (
                  <tr key={service.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10 bg-indigo-100 rounded-lg flex items-center justify-center text-indigo-700">
                          <FiSettings size={18} />
                        </div>
                        <div className="ml-4">
                          <div
                            className="text-sm font-medium text-gray-900 hover:text-indigo-700 cursor-pointer"
                            onClick={() => handleViewDetails(service.id)}
                          >
                            {service.name}
                          </div>
                          <div className="text-sm text-gray-500">
                            {service.description.substring(0, 60)}...
                          </div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <FiDollarSign className="text-gray-500 mr-1" size={16} />
                        <span className="text-sm text-gray-900">{service.price}</span>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {service.processingTime}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                      <div className="flex space-x-3">
                        <button
                          onClick={() => handleViewDetails(service.id)}
                          className="text-blue-600 hover:text-blue-900 flex items-center"
                          title="View Details"
                        >
                          <FiEye className="mr-1" size={16} />
                        </button>
                        <button
                          onClick={() => handleEdit(service.id)}
                          className="text-indigo-600 hover:text-indigo-900 flex items-center"
                          title="Edit"
                        >
                          <FiEdit2 className="mr-1" size={16} />
                        </button>
                        <button
                          onClick={() => handleDelete(service.id)}
                          disabled={deleteLoading === service.id}
                          className="text-red-600 hover:text-red-900 flex items-center disabled:opacity-50"
                          title="Delete"
                        >
                          {deleteLoading === service.id ? (
                            <div className="animate-spin rounded-full h-4 w-4 border-t-2 border-b-2 border-red-600"></div>
                          ) : (
                            <FiTrash2 className="mr-1" size={16} />
                          )}
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        ) : (
          <div className="text-center py-12">
            <FiSettings className="mx-auto h-12 w-12 text-gray-400 mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">No services found</h3>
            <p className="text-gray-500">
              {searchTerm
                ? 'Try adjusting your search criteria'
                : 'Get started by adding your first service'}
            </p>
            {!searchTerm && (
              <Link
                to="/admin/add-service"
                className="mt-4 inline-flex items-center px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700"
              >
                <FiPlus className="mr-2" />
                Add New Service
              </Link>
            )}
          </div>
        )}
      </div>
    </AdminLayout>
  );
}

// Service Form Component (Add/Edit) - Enhanced
function ServiceForm() {
  const { id } = useParams();
  const [isEditMode, setIsEditMode] = useState(!!id);
  const [activeTab, setActiveTab] = useState('service-details');
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState('');
  const [documentsRequired, setDocumentsRequired] = useState('');
  const [processingTime, setProcessingTime] = useState('');
  const [keyBenefits, setKeyBenefits] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [services, setServices] = useState([]);
  const [selectedService, setSelectedService] = useState(id || '');

  // Form builder states
  const [formTitle, setFormTitle] = useState('');
  const [formDescription, setFormDescription] = useState('');
  const [formFields, setFormFields] = useState([]);
  const [currentField, setCurrentField] = useState(null);
  const [showFieldConfig, setShowFieldConfig] = useState(false);
  const [fieldLabel, setFieldLabel] = useState('');
  const [fieldType, setFieldType] = useState('text');
  const [fieldRequired, setFieldRequired] = useState(false);
  const [fieldOptions, setFieldOptions] = useState(['Option 1', 'Option 2']);
  const [fieldPlaceholder, setFieldPlaceholder] = useState('');
  const [existingForm, setExistingForm] = useState(null);

  const navigate = useNavigate();

  // Fetch services and check for existing form on component mount
  useEffect(() => {
    fetchServices();

    // If in edit mode, load the service data
    if (isEditMode) {
      loadServiceData(id);
      setSelectedService(id);
    }
  }, [id, isEditMode]);

  // Load existing form when service is selected
  useEffect(() => {
    if (selectedService) {
      loadExistingForm(selectedService);
    } else {
      setFormFields([]);
      setFormTitle('');
      setFormDescription('');
      setExistingForm(null);
    }
  }, [selectedService]);

  // Set form title to service name when name changes
  useEffect(() => {
    if (name && activeTab === 'form-builder') {
      setFormTitle(name);
    }
  }, [name, activeTab]);

  const fetchServices = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, 'services'));
      const servicesList = [];

      querySnapshot.forEach((doc) => {
        const service = { id: doc.id, ...doc.data() };
        servicesList.push(service);
      });

      setServices(servicesList);
    } catch (err) {
      console.error('Error fetching services:', err);
      setError('Failed to load services: ' + err.message);
    }
  };

  const loadServiceData = async (serviceId) => {
    try {
      setLoading(true);
      const serviceDoc = await getDoc(doc(db, 'services', serviceId));
      if (serviceDoc.exists()) {
        const serviceData = serviceDoc.data();
        setName(serviceData.name || '');
        setDescription(serviceData.description || '');
        setPrice(serviceData.price || '');
        setDocumentsRequired(serviceData.documentsRequired?.join(', ') || '');
        setProcessingTime(serviceData.processingTime || '');
        setKeyBenefits(serviceData.keyBenefits?.join(', ') || '');
      } else {
        setError('Service not found');
        navigate('/admin/services');
      }
    } catch (err) {
      console.error('Error loading service:', err);
      setError('Failed to load service: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const loadExistingForm = async (serviceId) => {
    try {
      const formDoc = await getDoc(doc(db, 'forms', serviceId));
      if (formDoc.exists()) {
        const formData = formDoc.data();
        setFormTitle(formData.title || '');
        setFormDescription(formData.description || '');
        setFormFields(formData.fields || []);
        setExistingForm(formData);
      } else {
        setFormTitle(name || '');
        setFormDescription('');
        setFormFields([]);
        setExistingForm(null);
      }
    } catch (err) {
      console.error('Error loading form:', err);
      setError('Failed to load form: ' + err.message);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!name || !description || !price || !documentsRequired || !processingTime || !keyBenefits) {
      return setError('Please fill all required fields');
    }

    try {
      setLoading(true);
      setError('');

      const serviceData = {
        name,
        description,
        price: Number(price),
        documentsRequired: documentsRequired.split(',').map(doc => doc.trim()).filter(doc => doc),
        processingTime,
        keyBenefits: keyBenefits.split(',').map(benefit => benefit.trim()).filter(benefit => benefit),
        updatedAt: new Date()
      };

      if (isEditMode) {
        // Update existing service
        await updateDoc(doc(db, 'services', id), serviceData);
        setSuccess('Service updated successfully!');
      } else {
        // Add new service
        serviceData.createdAt = new Date();
        const docRef = await addDoc(collection(db, 'services'), serviceData);
        setSuccess('Service added successfully!');
        setSelectedService(docRef.id);
        setActiveTab('form-builder');
      }

      fetchServices(); // Refresh services list
    } catch (err) {
      setError(`Failed to ${isEditMode ? 'update' : 'add'} service: ` + err.message);
    } finally {
      setLoading(false);
    }
  };

  // Form Builder Functions
  const handleAddField = (type) => {
    const newField = {
      id: `field-${Date.now()}`,
      type,
      label: type.charAt(0).toUpperCase() + type.slice(1),
      required: false,
      placeholder: '',
    };

    // Only add options for field types that need them
    if (type === 'select' || type === 'radio' || type === 'checkbox') {
      newField.options = ['Option 1', 'Option 2'];
    }

    setFormFields([...formFields, newField]);
    setCurrentField(newField);
    setFieldLabel(newField.label);
    setFieldType(type);
    setFieldRequired(false);
    setFieldOptions(type === 'select' || type === 'radio' || type === 'checkbox'
      ? ['Option 1', 'Option 2']
      : []);
    setFieldPlaceholder('');
    setShowFieldConfig(true);
  };

  const handleEditField = (field) => {
    setCurrentField(field);
    setFieldLabel(field.label);
    setFieldType(field.type);
    setFieldRequired(field.required || false);
    setFieldOptions(field.options || []);
    setFieldPlaceholder(field.placeholder || '');
    setShowFieldConfig(true);
  };

  const handleSaveField = () => {
    if (!fieldLabel) {
      setError('Field label is required');
      return;
    }

    const updatedField = {
      ...currentField,
      label: fieldLabel,
      type: fieldType,
      required: fieldRequired,
      placeholder: fieldPlaceholder,
    };

    // Only include options for field types that need them
    if (['select', 'radio', 'checkbox'].includes(fieldType)) {
      updatedField.options = fieldOptions;
    } else {
      // Remove options property for field types that don't need them
      delete updatedField.options;
    }

    if (currentField.id) {
      // Update existing field
      setFormFields(formFields.map(field =>
        field.id === currentField.id ? updatedField : field
      ));
    } else {
      // Add new field
      setFormFields([...formFields, { ...updatedField, id: `field-${Date.now()}` }]);
    }

    setShowFieldConfig(false);
    setCurrentField(null);
  };

  const handleDeleteField = (id) => {
    setFormFields(formFields.filter(field => field.id !== id));
    if (currentField && currentField.id === id) {
      setShowFieldConfig(false);
      setCurrentField(null);
    }
  };

  const handleDragEnd = (result) => {
    if (!result.destination) return;

    const items = Array.from(formFields);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    setFormFields(items);
  };

  const handleAddOption = () => {
    setFieldOptions([...fieldOptions, `Option ${fieldOptions.length + 1}`]);
  };

  const handleRemoveOption = (index) => {
    const newOptions = [...fieldOptions];
    newOptions.splice(index, 1);
    setFieldOptions(newOptions);
  };

  const handleSaveForm = async () => {
    if (!selectedService) {
      setError('Please select a service first');
      return;
    }

    if (formFields.length === 0) {
      setError('Please add at least one form field');
      return;
    }

    if (!formTitle) {
      setError('Please enter a form title');
      return;
    }

    try {
      setLoading(true);

      // Clean up the form data before saving
      const formData = {
        serviceId: selectedService,
        name: formTitle,
        description: formDescription,
        fields: formFields.map(field => {
          // Create a clean copy of the field without any undefined properties
          const cleanField = {
            id: field.id,
            type: field.type,
            label: field.label,
            required: field.required || false,
            placeholder: field.placeholder || '',
          };

          // Only include options for field types that need them
          if (['select', 'radio', 'checkbox'].includes(field.type) && field.options) {
            cleanField.options = field.options;
          }

          return cleanField;
        }),
        createdAt: existingForm?.createdAt || new Date(),
        updatedAt: new Date()
      };

      await setDoc(doc(db, 'forms', selectedService), formData);

      setSuccess('Form template saved successfully!');
      setExistingForm(formData);

      // If we just created a new service and form, redirect to services list
      if (!isEditMode) {
        setTimeout(() => {
          navigate('/admin/services');
        }, 2000);
      }
    } catch (err) {
      setError('Failed to save form template: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const renderFieldPreview = (field) => {
    const commonProps = {
      className: "w-full px-3 py-2 border border-gray-300 rounded-md",
      placeholder: field.placeholder || `Enter ${field.label.toLowerCase()}`,
      disabled: true
    };

    switch (field.type) {
      case 'text':
      case 'email':
        return <input type={field.type} {...commonProps} />;

      case 'textarea':
        return <textarea rows="3" {...commonProps} />;

      case 'select':
        return (
          <select {...commonProps}>
            <option value="">Select an option</option>
            {field.options && field.options.map((option, index) => (
              <option key={index} value={option}>{option}</option>
            ))}
          </select>
        );

      case 'radio':
        return (
          <div className="space-y-2">
            {field.options && field.options.map((option, index) => (
              <label key={index} className="flex items-center">
                <input type="radio" name={`radio-${field.id}`} className="mr-2" disabled />
                {option}
              </label>
            ))}
          </div>
        );

      case 'checkbox':
        return (
          <div className="space-y-2">
            {field.options && field.options.map((option, index) => (
              <label key={index} className="flex items-center">
                <input type="checkbox" className="mr-2" disabled />
                {option}
              </label>
            ))}
          </div>
        );

      case 'date':
        return <input type="date" {...commonProps} />;

      case 'file':
        return (
          <div className="border border-dashed border-gray-300 rounded-md p-4 text-center">
            <FiUpload className="mx-auto text-gray-400 mb-2" />
            <p className="text-sm text-gray-500">Click to upload or drag and drop</p>
          </div>
        );

      default:
        return <input type="text" {...commonProps} />;
    }
  };

  const getFieldIcon = (type) => {
    switch (type) {
      case 'text': return <FiType size={16} />;
      case 'email': return <FiMail size={16} />;
      case 'textarea': return <FiEdit2 size={16} />;
      case 'select': return <FiList size={16} />;
      case 'radio': return <FiRadio size={16} />;
      case 'checkbox': return <FiCheckSquare size={16} />;
      case 'date': return <FiCalendar size={16} />;
      case 'file': return <FiUpload size={16} />;
      default: return <FiType size={16} />;
    }
  };

  return (
    <AdminLayout
      title={isEditMode ? 'Edit Service' : 'Add New Service'}
      activeNav="add-service"
    >
      {/* Tabs */}
      <div className="max-w-7xl mx-auto mb-6">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8">
            <button
              onClick={() => setActiveTab('service-details')}
              className={`py-4 px-1 border-b-2 font-medium text-sm ${activeTab === 'service-details'
                  ? 'border-indigo-500 text-indigo-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
            >
              Service Details
            </button>
            <button
              onClick={() => setActiveTab('form-builder')}
              className={`py-4 px-1 border-b-2 font-medium text-sm ${activeTab === 'form-builder'
                  ? 'border-indigo-500 text-indigo-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
            >
              Form Builder
            </button>
          </nav>
        </div>
      </div>

      {error && (
        <div className="max-w-7xl mx-auto bg-red-50 border-l-4 border-red-500 p-4 mb-6 rounded flex justify-between items-center">
          <p className="text-red-700">{error}</p>
          <button onClick={() => setError('')} className="text-red-600">
            <FiXCircle size={20} />
          </button>
        </div>
      )}

      {success && (
        <div className="max-w-7xl mx-auto bg-green-50 border-l-4 border-green-500 p-4 mb-6 rounded flex justify-between items-center">
          <p className="text-green-700">{success}</p>
          <button onClick={() => setSuccess('')} className="text-green-600">
            <FiXCircle size={20} />
          </button>
        </div>
      )}

      {activeTab === 'service-details' && (
        <div className="max-w-7xl mx-auto">
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
            <div className="p-6 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-800">Service Details</h2>
              <p className="text-sm text-gray-600">
                {isEditMode ? 'Edit the service details' : 'Add a new service to your catalog'}
              </p>
            </div>
            <div className="p-6">
              <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Service Name *</label>
                  <input
                    type="text"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="e.g. Passport Application"
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Description *</label>
                  <textarea
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 h-32"
                    placeholder="Detailed service description..."
                    required
                  />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Price () *</label>
                    <input
                      type="number"
                      value={price}
                      onChange={(e) => setPrice(e.target.value)}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      placeholder="0.00"
                      min="0"
                      step="0.01"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Processing Time *</label>
                    <input
                      type="text"
                      value={processingTime}
                      onChange={(e) => setProcessingTime(e.target.value)}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      placeholder="e.g. 7-10 business days"
                      required
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Documents Required (comma separated) *</label>
                  <textarea
                    value={documentsRequired}
                    onChange={(e) => setDocumentsRequired(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 h-20"
                    placeholder="e.g. Aadhar Card, PAN Card, Photo"
                    required
                  />
                  <p className="text-xs text-gray-500 mt-1">Separate multiple documents with commas</p>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Key Benefits (comma separated) *</label>
                  <textarea
                    value={keyBenefits}
                    onChange={(e) => setKeyBenefits(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 h-20"
                    placeholder="e.g. Fast processing, Expert assistance, Online tracking"
                    required
                  />
                  <p className="text-xs text-gray-500 mt-1">Separate multiple benefits with commas</p>
                </div>

                <div className="flex justify-end space-x-3 pt-4 border-t border-gray-200 mt-6">
                  <button
                    type="button"
                    onClick={() => navigate('/admin/services')}
                    className="flex items-center px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50"
                    disabled={loading}
                  >
                    <FiArrowLeft className="mr-2" />
                    Cancel
                  </button>
                  <button
                    type="submit"
                    disabled={loading}
                    className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 disabled:opacity-50 flex items-center"
                  >
                    {loading ? (
                      <>
                        <div className="animate-spin rounded-full h-4 w-4 border-t-2 border-b-2 border-white mr-2"></div>
                        {isEditMode ? 'Updating...' : 'Adding...'}
                      </>
                    ) : (
                      <>
                        <FiSave className="mr-2" />
                        {isEditMode ? 'Update Service' : 'Add Service'}
                      </>
                    )}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {activeTab === 'form-builder' && (
        <div className="max-w-7xl mx-auto">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-bold text-gray-800">Form Builder</h2>
            <div className="flex space-x-3">
              <button
                onClick={() => setActiveTab('service-details')}
                className="inline-flex justify-center py-2 px-4 border border-gray-300 shadow-sm text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              >
                Back to Service
              </button>
              <button
                onClick={handleSaveForm}
                disabled={!formTitle || formFields.length === 0 || loading || !selectedService}
                className={`inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 ${!formTitle || formFields.length === 0 || loading || !selectedService
                    ? 'bg-indigo-300 cursor-not-allowed'
                    : 'bg-indigo-600 hover:bg-indigo-700'
                  }`}
              >
                {loading ? (
                  <>
                    <div className="animate-spin rounded-full h-4 w-4 border-t-2 border-b-2 border-white mr-2"></div>
                    Saving...
                  </>
                ) : (
                  <>
                    <FiSave className="mr-2" />
                    {existingForm ? 'Update Form' : 'Save Form'}
                  </>
                )}
              </button>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-md overflow-hidden">
            <div className="p-6 border-b border-gray-200">
              <div className="flex flex-col space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700">Form Title *</label>
                  <input
                    type="text"
                    value={formTitle}
                    onChange={(e) => setFormTitle(e.target.value)}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Enter form title"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">Description</label>
                  <textarea
                    value={formDescription}
                    onChange={(e) => setFormDescription(e.target.value)}
                    rows="2"
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Enter form description"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">Select Service *</label>
                  <select
                    value={selectedService}
                    onChange={(e) => setSelectedService(e.target.value)}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                    required
                    disabled={isEditMode} // Disable if in edit mode
                  >
                    <option value="">Select a service</option>
                    {services.map(service => (
                      <option key={service.id} value={service.id}>{service.name}</option>
                    ))}
                  </select>
                  {selectedService && existingForm && (
                    <p className="mt-1 text-sm text-green-600">
                      This service already has a form. Editing will update the existing form.
                    </p>
                  )}
                  {isEditMode && (
                    <p className="mt-1 text-sm text-gray-500">
                      You cannot change the service while editing.
                    </p>
                  )}
                </div>
              </div>
            </div>

            <div className="flex flex-col lg:flex-row">
              {/* Form Fields Panel */}
              <div className="w-full lg:w-1/4 p-4 border-b lg:border-b-0 lg:border-r border-gray-200 bg-gray-50">
                <h3 className="text-sm font-medium text-gray-700 mb-3">Form Elements</h3>
                <div className="grid grid-cols-2 lg:grid-cols-1 gap-2">
                  <FieldTypeButton
                    icon={<FiType />}
                    label="Text"
                    onClick={() => handleAddField('text')}
                  />
                  <FieldTypeButton
                    icon={<FiMail />}
                    label="Email"
                    onClick={() => handleAddField('email')}
                  />
                  <FieldTypeButton
                    icon={<FiEdit2 />}
                    label="Textarea"
                    onClick={() => handleAddField('textarea')}
                  />
                  <FieldTypeButton
                    icon={<FiList />}
                    label="Dropdown"
                    onClick={() => handleAddField('select')}
                  />
                  <FieldTypeButton
                    icon={<FiRadio />}
                    label="Radio"
                    onClick={() => handleAddField('radio')}
                  />
                  <FieldTypeButton
                    icon={<FiCheckSquare />}
                    label="Checkbox"
                    onClick={() => handleAddField('checkbox')}
                  />
                  <FieldTypeButton
                    icon={<FiCalendar />}
                    label="Date"
                    onClick={() => handleAddField('date')}
                  />
                  <FieldTypeButton
                    icon={<FiUpload />}
                    label="File Upload"
                    onClick={() => handleAddField('file')}
                  />
                </div>
              </div>

              {/* Form Preview */}
              <div className="w-full lg:w-2/4 p-6 border-b lg:border-b-0 border-gray-200">
                <h3 className="text-sm font-medium text-gray-700 mb-3">Form Preview</h3>
                {formFields.length === 0 ? (
                  <div className="text-center py-12 bg-gray-50 rounded-lg border-2 border-dashed border-gray-300">
                    <FiFileText className="mx-auto h-12 w-12 text-gray-400" />
                    <h3 className="mt-2 text-sm font-medium text-gray-900">No fields added</h3>
                    <p className="mt-1 text-xs text-gray-500">Add form elements from the left panel</p>
                  </div>
                ) : (
                  <DragDropContext onDragEnd={handleDragEnd}>
                    <Droppable droppableId="formFields">
                      {(provided) => (
                        <div
                          {...provided.droppableProps}
                          ref={provided.innerRef}
                          className="space-y-4"
                        >
                          {formFields.map((field, index) => (
                            <Draggable key={field.id} draggableId={field.id} index={index}>
                              {(provided) => (
                                <div
                                  ref={provided.innerRef}
                                  {...provided.draggableProps}
                                  className={`relative group border ${currentField?.id === field.id ? 'border-indigo-300 bg-indigo-50' : 'border-transparent hover:border-gray-200'} rounded-md p-4 bg-white`}
                                >
                                  <div
                                    {...provided.dragHandleProps}
                                    className="absolute top-2 left-2 opacity-0 group-hover:opacity-100 cursor-move"
                                  >
                                    <div className="w-4 h-4 bg-gray-200 rounded"></div>
                                  </div>
                                  <label className="block text-sm font-medium text-gray-700 mb-1">
                                    {field.label}
                                    {field.required && <span className="text-red-500 ml-1">*</span>}
                                  </label>
                                  {renderFieldPreview(field)}
                                  <div className="absolute top-2 right-2 opacity-0 group-hover:opacity-100 flex space-x-1">
                                    <button
                                      onClick={() => handleEditField(field)}
                                      className="p-1 bg-white rounded-md shadow-sm text-gray-600 hover:text-indigo-600"
                                      title="Edit"
                                    >
                                      <FiEdit2 size={14} />
                                    </button>
                                    <button
                                      onClick={() => handleDeleteField(field.id)}
                                      className="p-1 bg-white rounded-md shadow-sm text-gray-600 hover:text-red-600"
                                      title="Delete"
                                    >
                                      <FiTrash2 size={14} />
                                    </button>
                                  </div>
                                </div>
                              )}
                            </Draggable>
                          ))}
                          {provided.placeholder}
                        </div>
                      )}
                    </Droppable>
                  </DragDropContext>
                )}
              </div>

              {/* Field Configuration */}
              <div className="w-full lg:w-1/4 p-4 bg-gray-50">
                {showFieldConfig && currentField ? (
                  <div>
                    <h3 className="text-sm font-medium text-gray-700 mb-3">Field Settings</h3>
                    <div className="space-y-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700">Label *</label>
                        <input
                          type="text"
                          value={fieldLabel}
                          onChange={(e) => setFieldLabel(e.target.value)}
                          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700">Type</label>
                        <select
                          value={fieldType}
                          onChange={(e) => setFieldType(e.target.value)}
                          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                        >
                          <option value="text">Text</option>
                          <option value="email">Email</option>
                          <option value="textarea">Textarea</option>
                          <option value="select">Dropdown</option>
                          <option value="radio">Radio</option>
                          <option value="checkbox">Checkbox</option>
                          <option value="date">Date</option>
                          <option value="file">File Upload</option>
                        </select>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700">Placeholder</label>
                        <input
                          type="text"
                          value={fieldPlaceholder}
                          onChange={(e) => setFieldPlaceholder(e.target.value)}
                          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                          placeholder="Enter placeholder text"
                        />
                      </div>

                      <div className="flex items-center">
                        <input
                          type="checkbox"
                          checked={fieldRequired}
                          onChange={(e) => setFieldRequired(e.target.checked)}
                          className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                        />
                        <label className="ml-2 block text-sm text-gray-700">Required Field</label>
                      </div>

                      {(fieldType === 'select' || fieldType === 'radio' || fieldType === 'checkbox') && (
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Options</label>
                          <div className="mt-1 space-y-2">
                            {fieldOptions.map((option, index) => (
                              <div key={index} className="flex items-center">
                                <input
                                  type="text"
                                  value={option}
                                  onChange={(e) => {
                                    const newOptions = [...fieldOptions];
                                    newOptions[index] = e.target.value;
                                    setFieldOptions(newOptions);
                                  }}
                                  className="flex-1 block border border-gray-300 rounded-md shadow-sm py-1 px-2 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                                />
                                {fieldOptions.length > 1 && (
                                  <button
                                    onClick={() => handleRemoveOption(index)}
                                    className="ml-2 text-red-600 hover:text-red-800"
                                    title="Remove option"
                                  >
                                    <FiTrash2 size={14} />
                                  </button>
                                )}
                              </div>
                            ))}
                          </div>
                          <button
                            onClick={handleAddOption}
                            className="mt-2 inline-flex items-center px-2.5 py-1.5 border border-gray-300 shadow-sm text-xs font-medium rounded text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                          >
                            <FiPlus size={12} className="mr-1" /> Add Option
                          </button>
                        </div>
                      )}

                      <div className="pt-2">
                        <button
                          onClick={handleSaveField}
                          className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                        >
                          Save Changes
                        </button>
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="text-center py-12">
                    <FiSettings className="mx-auto h-12 w-12 text-gray-400" />
                    <h3 className="mt-2 text-sm font-medium text-gray-900">No field selected</h3>
                    <p className="mt-1 text-xs text-gray-500">Select a field to configure its properties</p>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </AdminLayout>
  );
}

// Shared Admin Layout Component
function AdminLayout({
  children,
  title,
  activeNav = "dashboard"
}) {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [showNotifications, setShowNotifications] = useState(false);
  const [showProfileMenu, setShowProfileMenu] = useState(false);
  const [notifications, setNotifications] = useState([]);
  const [unreadCount, setUnreadCount] = useState(0);
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      const auth = getAuth();
      await signOut(auth);
      // Redirect to admin login page after logout
      navigate('/admin/login');
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  // Fetch notifications
  useEffect(() => {
    const fetchNotifications = async () => {
      try {
        const notificationsQuery = query(
          collection(db, 'notifications'),
          orderBy('createdAt', 'desc'),
          limit(10)
        );
        const snapshot = await getDocs(notificationsQuery);
        const notificationsData = [];
        let unread = 0;

        snapshot.forEach((doc) => {
          const notification = { id: doc.id, ...doc.data() };
          notificationsData.push(notification);
          if (!notification.read) unread++;
        });

        setNotifications(notificationsData);
        setUnreadCount(unread);
      } catch (error) {
        console.error('Error fetching notifications:', error);
      }
    };

    fetchNotifications();
  }, []);

  // Lock body scroll when mobile sidebar is open
  useEffect(() => {
    if (mobileMenuOpen) {
      const originalStyle = document.body.style.overflow;
      document.body.style.overflow = 'hidden';
      return () => {
        document.body.style.overflow = originalStyle;
      };
    }
  }, [mobileMenuOpen]);

  const markNotificationAsRead = async (notificationId) => {
    try {
      await updateDoc(doc(db, 'notifications', notificationId), {
        read: true
      });

      setNotifications(notifications.map(notification =>
        notification.id === notificationId
          ? { ...notification, read: true }
          : notification
      ));

      setUnreadCount(prev => Math.max(0, prev - 1));
    } catch (error) {
      console.error('Error marking notification as read:', error);
    }
  };

  const formatNotificationTime = (timestamp) => {
    if (!timestamp) return 'Just now';
    
    const now = new Date();
    const notificationTime = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
    const diffInMinutes = Math.floor((now - notificationTime) / (1000 * 60));
    
    if (diffInMinutes < 1) return 'Just now';
    if (diffInMinutes < 60) return `${diffInMinutes}m ago`;
    if (diffInMinutes < 1440) return `${Math.floor(diffInMinutes / 60)}h ago`;
    return `${Math.floor(diffInMinutes / 1440)}d ago`;
  };

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Enhanced Sidebar Component */}
      <Sidebar
        collapsed={sidebarCollapsed}
        onToggle={() => setSidebarCollapsed(!sidebarCollapsed)}
        mobileMenuOpen={mobileMenuOpen}
        onMobileToggle={() => setMobileMenuOpen((prev) => !prev)}
      />

      {/* Main content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Top bar */}
        <header className="bg-white shadow-sm border-b border-gray-200 px-6 py-4 sticky top-0 z-20">
          <div className="flex items-center justify-between">
            <button
              onClick={() => setMobileMenuOpen(true)}
              className="p-2 rounded-lg hover:bg-gray-100 md:hidden"
              aria-label="Open sidebar menu"
              aria-controls="admin-sidebar"
              aria-expanded={mobileMenuOpen}
            >
              <FiMenu size={20} />
            </button>
            <h1 className="text-2xl font-bold text-gray-900 capitalize">
              {activeNav.replace('-', ' ')}
            </h1>
            <div className="flex items-center space-x-4">
              <span className="text-sm text-gray-600">Admin Panel</span>

              {/* Notifications Button */}
              <div className="relative">
                <button
                  onClick={() => setShowNotifications(!showNotifications)}
                  className="relative p-2 rounded-lg hover:bg-gray-100"
                >
                  <FiBell size={20} />
                  {unreadCount > 0 && (
                    <span className="absolute -top-1 -right-1 bg-red-500 text-white text-xs rounded-full h-5 w-5 flex items-center justify-center">
                      {unreadCount}
                    </span>
                  )}
                </button>

                {/* Notifications Dropdown */}
                {showNotifications && (
                  <div className="absolute right-0 mt-2 w-80 bg-white rounded-lg shadow-lg border border-gray-200 z-50">
                    <div className="p-4 border-b border-gray-200">
                      <div className="flex justify-between items-center">
                        <h3 className="text-lg font-semibold text-gray-800">Notifications</h3>
                        <span className="text-sm text-gray-500">{unreadCount} unread</span>
                      </div>
                    </div>
                    <div className="max-h-96 overflow-y-auto">
                      {notifications.length > 0 ? (
                        notifications.map((notification) => (
                          <div
                            key={notification.id}
                            className={`p-4 border-b border-gray-100 hover:bg-gray-50 cursor-pointer ${!notification.read ? 'bg-blue-50' : ''
                              }`}
                            onClick={() => markNotificationAsRead(notification.id)}
                          >
                            <div className="flex justify-between items-start">
                              <div className="flex-1">
                                <p className="text-sm font-medium text-gray-800">
                                  {notification.title}
                                </p>
                                <p className="text-sm text-gray-600 mt-1">
                                  {notification.message}
                                </p>
                              </div>
                              {!notification.read && (
                                <div className="w-2 h-2 bg-blue-500 rounded-full ml-2 mt-1"></div>
                              )}
                            </div>
                            <p className="text-xs text-gray-500 mt-2">
                              {formatNotificationTime(notification.createdAt)}
                            </p>
                          </div>
                        ))
                      ) : (
                        <div className="p-8 text-center text-gray-500">
                          <FiBell className="mx-auto h-8 w-8 mb-2" />
                          <p>No notifications yet</p>
                        </div>
                      )}
                    </div>
                  </div>
                )}
              </div>

              {/* Profile Menu */}
              <div className="relative">
                <button
                  onClick={() => setShowProfileMenu(!showProfileMenu)}
                  className="flex items-center space-x-2 p-2 rounded-lg hover:bg-gray-100"
                >
                  <div className="w-8 h-8 rounded-full bg-indigo-100 flex items-center justify-center text-indigo-700">
                    <FiUser size={18} />
                  </div>
                  <span className="hidden md:inline text-sm font-medium">Admin</span>
                  <FiChevronDown size={16} className="text-gray-500" />
                </button>

                {/* Profile Dropdown */}
                {showProfileMenu && (
                  <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-200 z-50">
                    <div className="p-2">
                      <div className="px-3 py-2 border-b border-gray-100">
                        <p className="text-sm font-medium text-gray-800">Admin User</p>
                        <p className="text-xs text-gray-500">admin@example.com</p>
                      </div>
                      <button
                        onClick={() => {
                          setShowProfileMenu(false);
                          // Add profile settings navigation here
                        }}
                        className="w-full text-left px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-md flex items-center"
                      >
                        <FiUser className="mr-2" size={16} />
                        Profile Settings
                      </button>
                      <button
                        onClick={() => {
                          setShowProfileMenu(false);
                          // Add preferences navigation here
                        }}
                        className="w-full text-left px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-md flex items-center"
                      >
                        <FiSettings className="mr-2" size={16} />
                        Preferences
                      </button>
                      <div className="border-t border-gray-100 mt-1 pt-1">
                        <button
                          onClick={() => {
                            setShowProfileMenu(false);
                            handleLogout();
                          }}
                          className="w-full text-left px-3 py-2 text-sm text-red-600 hover:bg-red-50 rounded-md flex items-center"
                        >
                          <FiLogOut className="mr-2" size={16} />
                          Sign Out
                        </button>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        </header>

          {/* Content */}
          <main className="flex-1 overflow-y-auto p-4 sm:p-6 bg-gray-50">
            {children}
          </main>
        </div>
      </div>
  );
}

// Sidebar Link Component
function SidebarLink({ to, icon, text, active = false, collapsed }) {
  return (
    <Link
      to={to}
      className={`flex items-center p-3 mx-2 my-1 rounded-lg transition-colors 
        ${active ? 'bg-indigo-600 bg-opacity-25' : 'hover:bg-indigo-600 hover:bg-opacity-25'}`}
      title={collapsed ? text : undefined}
    >
      <span className={`${collapsed ? 'mx-auto' : 'mr-3'}`}>{icon}</span>
      {!collapsed && <span>{text}</span>}
    </Link>
  );
}



// ContractManagement Component
function ContractManagement() {
  const [contracts, setContracts] = useState([]);
  const [loading, setLoading] = useState(true);
  const { showSuccess, showError } = useNotification();

  useEffect(() => {
    fetchContracts();
  }, []);

  const fetchContracts = async () => {
    try {
      setLoading(true);
      const contractsSnapshot = await getDocs(collection(db, 'contracts'));
      const contractsData = contractsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setContracts(contractsData);
    } catch (error) {
      console.error('Error fetching contracts:', error);
      showError('Failed to load contracts');
    } finally {
      setLoading(false);
    }
  };

  return (
    <AdminLayout>
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold text-gray-900">Contract Management</h1>
          <button className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 flex items-center">
            <FiPlus className="mr-2" />
            New Contract
          </button>
        </div>

        <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
          {loading ? (
            <div className="flex justify-center items-center h-64">
              <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Contract ID
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Vendor
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Value
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Status
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {contracts.map((contract) => (
                    <tr key={contract.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {contract.contractId}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {contract.vendorName}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {contract.value?.toLocaleString()}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full 
                          ${contract.status === 'active' ? 'bg-green-100 text-green-800' :
                            contract.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                              'bg-red-100 text-red-800'}`}>
                          {contract.status}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                        <div className="flex space-x-2">
                          <button className="text-indigo-600 hover:text-indigo-900">
                            <FiEye />
                          </button>
                          <button className="text-blue-600 hover:text-blue-900">
                            <FiEdit2 />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </AdminLayout>
  );
}

// VendorDetails Component
function VendorDetails() {
  const { id } = useParams();
  const [vendor, setVendor] = useState(null);
  const [loading, setLoading] = useState(true);
  const { showError } = useNotification();

  useEffect(() => {
    fetchVendor();
  }, [id]);

  const fetchVendor = async () => {
    try {
      setLoading(true);
      const vendorDoc = await getDoc(doc(db, 'vendors', id));
      if (vendorDoc.exists()) {
        setVendor({ id: vendorDoc.id, ...vendorDoc.data() });
      }
    } catch (error) {
      console.error('Error fetching vendor:', error);
      showError('Failed to load vendor details');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <AdminLayout>
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
        </div>
      </AdminLayout>
    );
  }

  if (!vendor) {
    return (
      <AdminLayout>
        <div className="text-center py-8">
          <p className="text-gray-500">Vendor not found</p>
        </div>
      </AdminLayout>
    );
  }

  return (
    <AdminLayout>
      <div className="max-w-4xl mx-auto space-y-6">
        <div className="flex justify-between items-center">
          <div>
            <Link
              to="/admin/vendors"
              className="text-indigo-600 hover:text-indigo-800 flex items-center mb-2"
            >
              <FiArrowLeft className="mr-2" />
              Back to Vendors
            </Link>
            <h1 className="text-2xl font-bold text-gray-900">{vendor.companyName}</h1>
          </div>
          <Link
            to={`/admin/edit-vendor/${vendor.id}`}
            className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 flex items-center"
          >
            <FiEdit2 className="mr-2" />
            Edit Vendor
          </Link>
        </div>

        <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Vendor Information</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <h3 className="text-sm font-medium text-gray-500">Contact Person</h3>
              <p className="mt-1 text-sm text-gray-900">{vendor.contactPerson}</p>
            </div>
            <div>
              <h3 className="text-sm font-medium text-gray-500">Email</h3>
              <p className="mt-1 text-sm text-gray-900">{vendor.email}</p>
            </div>
            <div>
              <h3 className="text-sm font-medium text-gray-500">Phone</h3>
              <p className="mt-1 text-sm text-gray-900">{vendor.phone}</p>
            </div>
            <div>
              <h3 className="text-sm font-medium text-gray-500">Category</h3>
              <p className="mt-1 text-sm text-gray-900">{vendor.category}</p>
            </div>
            <div>
              <h3 className="text-sm font-medium text-gray-500">Status</h3>
              <span className={`mt-1 px-2 inline-flex text-xs leading-5 font-semibold rounded-full 
                ${vendor.status === 'active' ? 'bg-green-100 text-green-800' :
                  vendor.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                    'bg-red-100 text-red-800'}`}>
                {vendor.status}
              </span>
            </div>
            <div>
              <h3 className="text-sm font-medium text-gray-500">Date Added</h3>
              <p className="mt-1 text-sm text-gray-900">
                {vendor.createdAt?.toDate().toLocaleDateString()}
              </p>
            </div>
          </div>
          {vendor.address && (
            <div className="mt-6">
              <h3 className="text-sm font-medium text-gray-500">Address</h3>
              <p className="mt-1 text-sm text-gray-900">{vendor.address}</p>
            </div>
          )}
        </div>
      </div>
    </AdminLayout>
  );
}

// Chart Components for Vendor Data
function SpendChart({ data }) {
  const chartData = Object.entries(data || {}).map(([month, amount]) => ({
    month,
    amount
  }));

  return (
    <ResponsiveContainer width="100%" height="100%">
      <AreaChart data={chartData}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="month" />
        <YAxis />
        <Tooltip formatter={(value) => [`${value.toLocaleString()}`, 'Spend']} />
        <Area type="monotone" dataKey="amount" stroke="#8884d8" fill="#8884d8" fillOpacity={0.3} />
      </AreaChart>
    </ResponsiveContainer>
  );
}

function VendorDistributionChart({ data }) {
  const chartData = Object.entries(data || {}).map(([category, count]) => ({
    name: category,
    value: count
  }));

  const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884D8'];

  return (
    <ResponsiveContainer width="100%" height="100%">
      <PieChart>
        <Pie
          data={chartData}
          cx="50%"
          cy="50%"
          labelLine={false}
          label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
          outerRadius={80}
          fill="#8884d8"
          dataKey="value"
        >
          {chartData.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>
        <Tooltip />
      </PieChart>
    </ResponsiveContainer>
  );
}

// Field Type Button Component
function FieldTypeButton({ icon, label, onClick }) {
  return (
    <button
      onClick={onClick}
      className="flex flex-col items-center justify-center p-3 bg-white rounded-md border border-gray-200 hover:border-indigo-300 hover:bg-indigo-50 transition-colors"
    >
      <span className="text-indigo-600 mb-1">{icon}</span>
      <span className="text-xs text-gray-700">{label}</span>
    </button>
  );
}

// Parent Company List Component
function ParentCompanyList() {
  const { showSuccess, showError } = useNotification();
  const [companies, setCompanies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    fetchCompanies();
  }, []);

  const fetchCompanies = async () => {
    try {
      const companiesSnapshot = await getDocs(collection(db, 'parentCompanies'));
      const companiesData = [];
      companiesSnapshot.forEach((doc) => {
        companiesData.push({ id: doc.id, ...doc.data() });
      });
      setCompanies(companiesData);
    } catch (error) {
      console.error('Error fetching companies:', error);
      showError('Failed to load companies');
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteCompany = async (companyId) => {
    if (window.confirm('Are you sure? This will affect all associated vendors.')) {
      try {
        await deleteDoc(doc(db, 'parentCompanies', companyId));
        setCompanies(companies.filter(c => c.id !== companyId));
        showSuccess('Company deleted successfully');
      } catch (error) {
        console.error('Error deleting company:', error);
        showError('Failed to delete company');
      }
    }
  };

  const filteredCompanies = companies.filter(company =>
    company.name?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <AdminLayout title="Parent Companies" activeNav="parent-companies">
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold text-gray-900">Parent Companies</h1>
          <Link
            to="/admin/add-parent-company"
            className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 flex items-center"
          >
            <FiPlus className="mr-2" />
            Add Parent Company
          </Link>
        </div>

        <div className="bg-white p-4 rounded-lg shadow-sm border border-gray-200">
          <div className="relative">
            <FiSearch className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
            <input
              type="text"
              placeholder="Search companies..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10 w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-indigo-500"
            />
          </div>
        </div>

        <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
          {loading ? (
            <div className="flex justify-center items-center h-64">
              <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-500"></div>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Company Name
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Contact Info
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Vendors Count
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {filteredCompanies.map((company) => (
                    <tr key={company.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div>
                          <div className="text-sm font-medium text-gray-900">{company.name}</div>
                          <div className="text-sm text-gray-500">{company.industry}</div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">{company.contactPerson}</div>
                        <div className="text-sm text-gray-500">{company.email}</div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-blue-100 text-blue-800">
                          {company.vendorCount || 0} vendors
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                        <div className="flex space-x-2">
                          <Link
                            to={`/admin/edit-parent-company/${company.id}`}
                            className="text-blue-600 hover:text-blue-900"
                          >
                            <FiEdit2 />
                          </Link>
                          <button
                            onClick={() => handleDeleteCompany(company.id)}
                            className="text-red-600 hover:text-red-900"
                          >
                            <FiTrash2 />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </AdminLayout>
  );
}