import { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { getAuth, signOut } from 'firebase/auth';
import {
  FiGrid, FiUsers, FiPlus, FiClipboard, FiFileText,
  FiBarChart2, FiUser, FiLogOut, FiMenu, FiX, FiChevronDown,
  FiChevronRight, FiSettings, FiDollarSign, FiCalendar,
  FiTrendingUp, FiCheckCircle, FiMail, FiShield, FiUserPlus,
  FiHome, FiSearch, FiBell
} from 'react-icons/fi';

export default function Sidebar({ collapsed, onToggle, mobileMenuOpen, onMobileToggle }) {
  const location = useLocation();
  const navigate = useNavigate();
  const [expandedSections, setExpandedSections] = useState({
    vendors: location.pathname.startsWith('/admin/vendors') || 
             location.pathname.startsWith('/admin/add-vendor') || 
             location.pathname.startsWith('/admin/vendor-performance'),
    operations: location.pathname.startsWith('/admin/purchase-orders') || 
               location.pathname.startsWith('/admin/create-po') || 
               location.pathname.startsWith('/admin/delivery-challans') || 
               location.pathname.startsWith('/admin/create-challan') || 
               location.pathname.startsWith('/admin/items'),
    management: location.pathname.startsWith('/admin/status-manager') || 
               location.pathname.startsWith('/admin/daily-todos') || 
               location.pathname.startsWith('/admin/reports'),
    reports: location.pathname.startsWith('/admin/analytics') || 
            location.pathname.startsWith('/admin/customers')
  });

  // Auto-close mobile menu when route changes
  useEffect(() => {
    if (mobileMenuOpen) {
      onMobileToggle();
    }
  }, [location.pathname]);

  // Close on Escape key when mobile menu is open
  useEffect(() => {
    if (!mobileMenuOpen) return;
    
    const handleEscape = (e) => {
      if (e.key === 'Escape') {
        onMobileToggle();
      }
    };
    
    document.addEventListener('keydown', handleEscape);
    return () => document.removeEventListener('keydown', handleEscape);
  }, [mobileMenuOpen, onMobileToggle]);

  // Prevent body scrolling when mobile menu is open
  useEffect(() => {
    if (mobileMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }
    
    return () => {
      document.body.style.overflow = 'auto';
    };
  }, [mobileMenuOpen]);

  const handleLogout = async () => {
    try {
      const auth = getAuth();
      await signOut(auth);
      navigate('/admin/login');
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  const toggleSection = (section) => {
    setExpandedSections(prev => ({
      ...prev,
      [section]: !prev[section]
    }));
  };

  const isActive = (path) => {
    return location.pathname === path;
  };

  const isParentActive = (paths) => {
    return paths.some(path => location.pathname.startsWith(path));
  };

  // Handle overlay click to close mobile menu
  const handleOverlayClick = () => {
    onMobileToggle();
  };

  return (
    <>
      {/* Mobile overlay */}
      {mobileMenuOpen && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
          onClick={handleOverlayClick}
          aria-hidden="true"
        />
      )}
      
      {/* Sidebar */}
      <aside 
        id="admin-sidebar"
        className={`fixed inset-y-0 left-0 transform ${
          mobileMenuOpen ? 'translate-x-0' : '-translate-x-full'
        } lg:relative lg:translate-x-0 transition-transform duration-300 ease-in-out z-50 
        ${collapsed ? 'w-16' : 'w-64'} bg-gray-900 text-white shadow-xl flex flex-col`}
        aria-label="Sidebar"
      >
        {/* Header */}
        <div className="p-4 border-b border-gray-700 flex-shrink-0">
          <div className="flex items-center justify-between">
            {!collapsed ? (
              <div className="flex items-center">
                <div className="w-8 h-8 rounded-md bg-indigo-600 flex items-center justify-center mr-2">
                  <FiHome size={16} />
                </div>
                <h2 className="text-xl font-bold text-white">VendorFlow</h2>
              </div>
            ) : (
              <div className="w-8 h-8 rounded-md bg-indigo-600 flex items-center justify-center mx-auto">
                <FiHome size={16} />
              </div>
            )}
            
            {/* Desktop toggle button */}
            <button 
              onClick={onToggle}
              className="p-1.5 rounded-lg hover:bg-gray-700 hidden lg:block transition-colors"
              aria-label={collapsed ? "Expand sidebar" : "Collapse sidebar"}
            >
              {collapsed ? <FiMenu size={18} /> : <FiX size={18} />}
            </button>
            
            {/* Mobile close button */}
            <button 
              onClick={onMobileToggle}
              className="p-1.5 rounded-lg hover:bg-gray-700 lg:hidden transition-colors"
              aria-label="Close menu"
            >
              <FiX size={18} />
            </button>
          </div>
        </div>
        
        {/* Search bar (visible when not collapsed) */}
        {!collapsed && (
          <div className="px-3 py-3 border-b border-gray-700 flex-shrink-0">
            <div className="relative">
              <FiSearch className="absolute left-3 top-3 text-gray-400" size={16} />
              <input 
                type="text" 
                placeholder="Search..." 
                className="w-full bg-gray-800 text-white pl-9 pr-3 py-2 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>
          </div>
        )}
        
        {/* Navigation */}
        <nav className="flex-1 overflow-y-auto py-4" role="navigation" aria-label="Main navigation">
          <div className="px-2">
            <SidebarItem 
              to="/admin/dashboard" 
              icon={<FiGrid size={20} />}
              text="Dashboard" 
              active={isActive('/admin/dashboard')}
              collapsed={collapsed}
            />
          </div>
          
          {/* Vendor Management Section */}
          <SidebarSection
            title="Vendor Management"
            icon={<FiUsers size={18} />}
            collapsed={collapsed}
            expanded={expandedSections.vendors}
            onToggle={() => toggleSection('vendors')}
            active={isParentActive(['/admin/vendors', '/admin/add-vendor', '/admin/vendor-performance'])}
          >
            <SidebarItem 
              to="/admin/vendors" 
              icon={<FiUsers size={18} />}
              text="All Vendors" 
              active={isActive('/admin/vendors')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/add-vendor" 
              icon={<FiPlus size={18} />}
              text="Add Vendor" 
              active={isActive('/admin/add-vendor')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/vendor-performance" 
              icon={<FiTrendingUp size={18} />}
              text="Performance" 
              active={isActive('/admin/vendor-performance')}
              collapsed={collapsed}
              isChild
            />
          </SidebarSection>
          
          {/* Operations Management Section */}
          <SidebarSection
            title="Operations Management"
            icon={<FiClipboard size={18} />}
            collapsed={collapsed}
            expanded={expandedSections.operations}
            onToggle={() => toggleSection('operations')}
            active={isParentActive(['/admin/purchase-orders', '/admin/create-po', '/admin/delivery-challans', '/admin/create-challan', '/admin/items'])}
          >
            <SidebarItem 
              to="/admin/purchase-orders" 
              icon={<FiFileText size={18} />}
              text="All POs" 
              active={isActive('/admin/purchase-orders')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/create-po" 
              icon={<FiDollarSign size={18} />}
              text="Create PO" 
              active={isActive('/admin/create-po')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/delivery-challans" 
              icon={<FiClipboard size={18} />}
              text="Delivery Challans" 
              active={isActive('/admin/delivery-challans')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/create-challan" 
              icon={<FiPlus size={18} />}
              text="Create Challan" 
              active={isActive('/admin/create-challan')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/items" 
              icon={<FiClipboard size={18} />}
              text="Items" 
              active={isActive('/admin/items')}
              collapsed={collapsed}
              isChild
            />
          </SidebarSection>
          
          {/* Management Tools Section */}
          <SidebarSection
            title="Management Tools"
            icon={<FiSettings size={18} />}
            collapsed={collapsed}
            expanded={expandedSections.management}
            onToggle={() => toggleSection('management')}
            active={isParentActive(['/admin/status-manager', '/admin/daily-todos', '/admin/reports'])}
          >
            <SidebarItem 
              to="/admin/status-manager" 
              icon={<FiCheckCircle size={18} />}
              text="Status Manager" 
              active={isActive('/admin/status-manager')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/daily-todos" 
              icon={<FiCalendar size={18} />}
              text="Daily Todos" 
              active={isActive('/admin/daily-todos')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/reports" 
              icon={<FiBarChart2 size={18} />}
              text="Reports" 
              active={isActive('/admin/reports')}
              collapsed={collapsed}
              isChild
            />
          </SidebarSection>
          
          {/* Reports & Analytics Section */}
          <SidebarSection
            title="Reports & Analytics"
            icon={<FiBarChart2 size={18} />}
            collapsed={collapsed}
            expanded={expandedSections.reports}
            onToggle={() => toggleSection('reports')}
            active={isParentActive(['/admin/analytics', '/admin/customers'])}
          >
            <SidebarItem 
              to="/admin/analytics" 
              icon={<FiTrendingUp size={18} />}
              text="Analytics" 
              active={isActive('/admin/analytics')}
              collapsed={collapsed}
              isChild
            />
            <SidebarItem 
              to="/admin/customers" 
              icon={<FiUser size={18} />}
              text="Customers" 
              active={isActive('/admin/customers')}
              collapsed={collapsed}
              isChild
            />
          </SidebarSection>
          
          {/* Quick Actions (when collapsed) */}
          {collapsed && (
            <div className="mt-4 pt-4 border-t border-gray-700">
              <div className="px-2 space-y-1">
                <SidebarItem 
                  to="/admin/add-vendor" 
                  icon={<FiPlus size={20} />}
                  text="Add Vendor" 
                  active={isActive('/admin/add-vendor')}
                  collapsed={collapsed}
                  tooltip="Add Vendor"
                />
                <SidebarItem 
                  to="/admin/create-po" 
                  icon={<FiFileText size={20} />}
                  text="Create PO" 
                  active={isActive('/admin/create-po')}
                  collapsed={collapsed}
                  tooltip="Create PO"
                />
                <SidebarItem 
                  to="/admin/daily-todos" 
                  icon={<FiCalendar size={20} />}
                  text="Daily Todos" 
                  active={isActive('/admin/daily-todos')}
                  collapsed={collapsed}
                  tooltip="Daily Todos"
                />
              </div>
            </div>
          )}
        </nav>
        
        {/* User section and Logout */}
        <div className="border-t border-gray-700 bg-gray-800 flex-shrink-0">
          {!collapsed && (
            <div className="p-3 border-b border-gray-700">
              <div className="flex items-center">
                <div className="w-8 h-8 rounded-full bg-indigo-600 flex items-center justify-center text-white text-sm font-medium mr-3">
                  A
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-medium truncate">Admin User</p>
                  <p className="text-xs text-gray-400 truncate">admin@example.com</p>
                </div>
                <button className="p-1.5 rounded-lg hover:bg-gray-700 transition-colors">
                  <FiBell size={16} className="text-gray-400" />
                </button>
              </div>
            </div>
          )}
          
          <button
            onClick={handleLogout}
            className={`flex items-center p-3 transition-colors w-full hover:bg-gray-700 ${collapsed ? 'justify-center' : ''}`}
            title={collapsed ? "Logout" : undefined}
          >
            <FiLogOut size={20} className={collapsed ? '' : 'mr-3'} />
            {!collapsed && <span className="font-medium">Logout</span>}
          </button>
        </div>
      </aside>

      {/* Mobile menu toggle button (only visible on mobile) */}
      {!mobileMenuOpen && (
        <button
          onClick={onMobileToggle}
          className="fixed bottom-4 left-4 z-40 p-3 bg-indigo-600 text-white rounded-full shadow-lg lg:hidden"
          aria-label="Open menu"
        >
          <FiMenu size={24} />
        </button>
      )}
    </>
  );
}

// Sidebar Section Component
function SidebarSection({ title, icon, children, collapsed, expanded, onToggle, active }) {
  if (collapsed) {
    return null;
  }

  return (
    <div className="mt-1 px-2">
      <button
        onClick={onToggle}
        className={`flex items-center justify-between w-full px-3 py-2.5 rounded-md text-sm font-medium transition-all
          ${active ? 'text-white bg-indigo-700' : 'text-gray-300 hover:text-white hover:bg-gray-800'}`}
        aria-expanded={expanded}
        aria-controls={`section-${title.toLowerCase().replace(/\s+/g, '-')}`}
      >
        <div className="flex items-center">
          <span className="text-gray-400 mr-2">{icon}</span>
          <span>{title}</span>
        </div>
        {expanded ? <FiChevronDown size={16} /> : <FiChevronRight size={16} />}
      </button>
      
      {expanded && (
        <div 
          id={`section-${title.toLowerCase().replace(/\s+/g, '-')}`} 
          className="mt-1 ml-4 space-y-1 border-l border-gray-700 pl-2 py-1"
        >
          {children}
        </div>
      )}
    </div>
  );
}

// Sidebar Item Component
function SidebarItem({ to, icon, text, active, collapsed, isChild = false, tooltip }) {
  const baseClasses = `flex items-center transition-all relative group rounded-md
    ${isChild ? 'py-2 px-3 text-sm' : 'p-3 text-medium'}
    ${active 
      ? 'bg-indigo-600 text-white shadow' 
      : 'text-gray-300 hover:bg-gray-800 hover:text-white'
    }`;

  return (
    <Link
      to={to}
      className={baseClasses}
      title={collapsed ? tooltip || text : undefined}
      aria-current={active ? 'page' : undefined}
    >
      <span className={`flex-shrink-0 ${collapsed ? 'mx-auto' : isChild ? 'mr-3' : 'mr-3'}`}>
        {icon}
      </span>
      {!collapsed && (
        <span className="truncate">
          {text}
        </span>
      )}
      
      {/* Tooltip for collapsed state */}
      {collapsed && (tooltip || text) && (
        <div className="absolute left-full ml-2 px-2 py-1.5 bg-gray-900 text-white text-xs rounded-md opacity-0 group-hover:opacity-100 transition-opacity shadow-lg whitespace-nowrap z-50 pointer-events-none">
          {tooltip || text}
        </div>
      )}
    </Link>
  );
}